import React, { useEffect, useState } from "react";
import axiosInstance from "../../../actions/axios";
import { Link } from "react-router-dom";
import { updateTitle } from "../../common/UpdateTitle";

const QBankPublished = () => {
  const [qbank, setQbank] = useState([]);
  useEffect(() => {
    axiosInstance.get("/qbank/question-bank").then((res) => {
      setQbank(res.data);
    });
    updateTitle({
      title: "Question Bank",
    });
  }, []);

  return (
    <div className="pt-32pt pt-sm-64pt pb-32pt">
      <div className="container page__container page-section pt-32pt pt-sm-64pt pb-32pt">
        <h1 className="h2 mb-0">Question Bank</h1>
        <ol className="breadcrumb m-0 p-0">
          <li className="breadcrumb-item">
            <a href="index.html">Home</a>
          </li>
          <li className="breadcrumb-item active">Question Bank</li>
        </ol>
      </div>
      <div className="container page__container page-section">
        <div className="page-separator">
          <div className="page-separator__text">Published</div>
        </div>

        <div className="row card-group-row mb-lg-8pt">
          {qbank ? (
            qbank.map((qbank) => (
              <div className="col-sm-4 card-group-row__col" key={qbank.id}>
                <div className="card overlay--show card-lg overlay--primary-dodger-blue stack stack--1 card-group-row__card">
                  <div className="card-body d-flex flex-column">
                    <div className="d-flex align-items-center">
                      <div className="flex">
                        <div className="d-flex align-items-center">
                          <div className="flex">
                            <div className="card-title">{qbank.name}</div>
                          </div>
                        </div>
                      </div>
                    </div>

                    <p className="mt-16pt text-black-70 flex">
                      {qbank.description.slice(0, 100)}...
                    </p>

                    <div className="d-flex align-items-start mb-8pt justify-content-start">
                      <div className="d-flex align-items-center mr-8pt">
                        <span className="material-icons icon-16pt text-black-50 mr-4pt">
                          monetization_on
                        </span>
                        <p className="flex text-black-50 lh-1 mb-0">
                          <small>{qbank.price} BDT</small>
                        </p>
                      </div>
                    </div>
                    <div className="d-flex align-items-end justify-content-end">
                      <Link
                        to={`/qbank/details/${qbank.name}`}
                        className="btn btn-primary"
                      >
                        Details
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            ))
          ) : (
            <div className="col-sm card-group-row__col">
              <p className="mt-16pt text-black-70 flex">Empty</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default QBankPublished;
